import React, { useState } from "react";
import { Row, Col } from "antd";
import { useTranslation } from "react-i18next";

import WALLET from "../../../constants/wallets";

import { useWallet } from "../../../hooks/useWallet";
import { truncAddress } from "../../../utils/utils";
import Button from "../../UI/Button/Button";
import CustomModal from "../../UI/CustomModal/CustomModal";

import styles from "./WalletButton.module.scss";
import classNames from "classnames";

const WalletList = ({ toggleShowModal }) => {
  const { connect } = useWallet();
  return (
    <div className={styles.WalletList}>
      <Row gutter={[16, 16]}>
        {Object.values(WALLET).map((item) => (
          <Col span={12} key={item.type}>
            <div className={styles.square}>
              <div
                className={classNames(styles.walletItem)}
                onClick={() => {
                  connect(item);
                  toggleShowModal();
                }}
              >
                <img
                  src={`/static/images/wallets/${item.type}.png`}
                  alt={item.name}
                />
                <p>{item.name}</p>
              </div>
            </div>
          </Col>
        ))}
      </Row>
      {/* <List
        grid={{
          gutter: 32,
          xs: 2,
          sm: 2,
          md: 2,
          lg: 2,
          xl: 2,
          xxl: 2,
        }}
        dataSource={Object.values(WALLET)}
        renderItem={(item) => (
          <List.Item>
            <div
              className={styles.walletItem}
              onClick={() => {
                connect(item);
                toggleShowModal();
              }}
              key={item.type}
            >
              <img
                src={`/static/images/wallets/${item.type}.png`}
                alt={item.name}
              />
              <p>{item.name}</p>
            </div>
          </List.Item>
        )}
      /> */}
    </div>
  );
};

const WalletButton = ({ onClick = () => {} }) => {
  const { account, disconnect } = useWallet();
  const [showModal, setShowModal] = useState(false);
  const { t } = useTranslation();

  const toggleShowModal = () => {
    setShowModal((prev) => !prev);
  };

  return (
    <>
      <div className={styles.WalletButton} onClick={onClick}>
        {account && (
          <Button
            secondary
            className={styles.walletButton}
            onClick={disconnect}
          >
            {truncAddress(account)}
          </Button>
        )}
        {!account && (
          <Button
            secondary
            className={styles.walletButton}
            onClick={toggleShowModal}
          >
            {t("Connect Wallet")}
          </Button>
        )}
      </div>
      <CustomModal
        visible={showModal}
        onCancel={toggleShowModal}
        title={t("Connect Wallet")}
        showTitle
        width={400}
      >
        <WalletList toggleShowModal={toggleShowModal} />
      </CustomModal>
    </>
  );
};

export default WalletButton;
