import React, { useEffect, useState } from "react";
import { Checkbox, Input, message } from "antd";
// import { RiseOutlined } from "@ant-design/icons";
import { useTranslation } from "react-i18next";

import MyDocument from "../../Components/PDF/PdfGenerate";
import { BlobProvider } from "@react-pdf/renderer";
import Button from "../../Components/UI/Button/Button";
import MainContainer from "../../Components/UI/MainContainer/MainContainer";
import { useInviterRecords, useIsRegistered } from "../../hooks/useInvitation";
import { useGlobalConfig, useInvest } from "../../hooks/useInvestPool";

import {
  useTokenAllowance,
  useTokenApprove,
  useTokenBalance,
} from "../../hooks/useToken";

import { useWallet } from "../../hooks/useWallet";
// import CustomModal from "../../Components/UI/CustomModal/CustomModal";
import styles from "./Investment.module.scss";
import { INVESTPOOL_ADDRESS, TOKENS } from "../../constants/addresses";
import { formatNumber } from "../../utils/utils";
import WalletButton from "../../Components/Header/WalletButton/WalletButton";
import ProfileButton from "../../Components/Profile/ProfileButton/ProfileButton";

// const SAFT_LINK_TEXT = "I Agree BGF Simple Agreement for Future Tokens";

const Investment = () => {
  const [checked, setChecked] = useState(false);
  const [investAmount, setInvestAmount] = useState("0");
  const [calculatedBGF, setCalculatedBGF] = useState(0);
  const [myDoc, setMyDoc] = useState(<MyDocument props={{}} />);

  const { data: isRegistered, isLoading: isRegisteredLoading } =
    useIsRegistered();
  const { data: globalConfig, isLoading: globalConfigLoading } =
    useGlobalConfig();
  const { data: inviterRecords, isLoading: inviterRecordsLoading } =
    useInviterRecords();
  // const { data: nativeBalance } = useTokenBalance(TOKENS.NATIVE);
  const { data: acceptBalance } = useTokenBalance(TOKENS.ACCEPT);
  const { data: acceptTokenAllowance } = useTokenAllowance(
    TOKENS.ACCEPT,
    INVESTPOOL_ADDRESS
  );
  const { state: approveState, approve } = useTokenApprove(TOKENS.ACCEPT);
  const { state: investState, invest } = useInvest();
  const { minimalSourceAmount, price } = globalConfig;
  const { nickname, email } = inviterRecords;
  const { account } = useWallet();
  const { t } = useTranslation();

  let formattedBal = formatNumber(acceptBalance);

  useEffect(() => {
    if (!(account && isRegistered)) {
      return;
    }
    const date = new Date();
    const pdfProps = {
      purchaseAmount: investAmount,
      year: date.getFullYear(),
      month: date.getUTCMonth() + 1,
      day: date.getUTCDate(),
      amountOfBGF: Number(investAmount) * price,
      purchaserName: checked && nickname,
      purchaserEmail: checked && email,
    };
    setMyDoc(<MyDocument props={pdfProps} />);
  }, [investAmount, email, nickname, price, account, isRegistered, checked]);

  useEffect(() => {
    if (minimalSourceAmount) {
      setInvestAmount(minimalSourceAmount);
    }
  }, [minimalSourceAmount]);

  useEffect(() => {
    const _investAmount = Number(investAmount);
    const _price = Number(price);
    if (!(Number.isNaN(_investAmount) || Number.isNaN(_price))) {
      setCalculatedBGF(_investAmount * _price);
    }
  }, [investAmount, price]);

  const handleCheck = (e) => {
    setChecked(e.target.checked);
  };

  const handleInvestAmountChange = (e) => {
    const value = e.target.value;
    if (Number.isNaN(Number(value))) {
      return;
    }
    setInvestAmount(value);
  };

  const handleInvest = () => {
    if (
      isRegisteredLoading ||
      globalConfigLoading ||
      inviterRecordsLoading ||
      investState?.isLoading
    ) {
      return;
    }
    if (!(account && isRegistered)) {
      message.info(t("Wallet Connet Alert"));
      return;
    }
    if (Number(investAmount < minimalSourceAmount)) {
      message.info(t("Minimum Invest Amount", { minimalSourceAmount }));
      return;
    }
    if (!checked) {
      message.info(t("Agree Saft"));
      return;
    }
    // if (nativeBalance < 0.005) {
    //   message.info(t("Need gas"));
    //   return;
    // }
    if (acceptBalance < investAmount) {
      message.info(t("Lack of Balance", { investAmount }));
      return;
    }
    invest(investAmount);
  };

  const handleApprove = async () => {
    if (approveState?.isLoading) {
      return;
    }
    if (!account) {
      message.info(t("Please connect to wallet"));
      return;
    }

    // check for gas
    // if (nativeBalance < 0.005) {
    //   message.info(t("Need gas"));
    //   return;
    // }
    approve(INVESTPOOL_ADDRESS);
  };

  const handleMaxAmount = () => {
    if (!acceptBalance) {
      return;
    }

    setInvestAmount(acceptBalance.toString());
  };

  const definedSuffix = (
    <p
      onClick={handleMaxAmount}
      style={{
        margin: "0",
        cursor: "pointer",
        textDecoration: "underline",
        fontStyle: "italic",
      }}
    >
      Max
    </p>
  );

  const getContent = () => {
    if (!account) {
      return (
        <>
          <h2 className={styles.title}>{t("Please Connect Wallet")}</h2>
          <WalletButton />
        </>
      );
    }
    if (!isRegistered) {
      return (
        <>
          <h2 className={styles.title}>{t("Please Sign Up")}</h2>
          <ProfileButton className={styles.investSignupButton} />
        </>
      );
    }
    return (
      <>
        <h2 className={styles.title}>{t("header-investment")}</h2>

        <div className={styles.price}>
          <img src="/static/images/Investment/rise.png" alt="rise" />
          <p>
            {`1 BGF ≈ ${formatNumber(price && 1 / price, "0,0.[000000]")} USDT`}
          </p>
        </div>
        <div className={styles.inputItem}>
          <div className={styles.amountInfo}>
            <h2>{t("Purchase Amount")}</h2>
            <p>{t("USDT Balance", { formattedBal })}</p>
          </div>
          <div className={styles.inputWrapper}>
            <Input
              suffix={definedSuffix}
              value={investAmount}
              onChange={handleInvestAmountChange}
            />
            <div className={styles.convertWrap}>
              <h2>USDT</h2>
              <h2>≈</h2>
              <h2>{calculatedBGF}</h2>
              <h2>BGF</h2>
            </div>
          </div>

          <p>• {t("Minimum Invest Amount", { minimalSourceAmount })}</p>
        </div>
        <div className={styles.inputItem}>
          <div className={styles.checkboxWrap}>
            <Checkbox onChange={handleCheck} />
            &nbsp;&nbsp;
            <BlobProvider document={myDoc}>
              {({ url, loading }) => (
                <a
                  href={url}
                  target="_blank"
                  rel="noopener noreferrer"
                  style={loading ? { pointerEvents: "none" } : null}
                >
                  {t("saft-link-text")}
                </a>
              )}
            </BlobProvider>
          </div>

          <p>• {t("Click To Agree")}</p>
        </div>
        {acceptTokenAllowance ? (
          <Button
            className={styles.submitButton}
            onClick={handleInvest}
            loading={investState?.isLoading}
          >
            {t("Purchase")}
          </Button>
        ) : (
          <Button
            className={styles.submitButton}
            onClick={handleApprove}
            loading={approveState?.isLoading}
          >
            {t("Approve USDT")}
          </Button>
        )}
      </>
    );
  };

  return (
    <MainContainer className={styles.Investment}>
      <div className={styles.imgWrap}>
        <img src="/static/images/Investment/invest.png" alt="invest" />
      </div>
      <div className={styles.contentWrap}>
        <div className={styles.investWrap}>{getContent()}</div>
      </div>
      {/* <CustomModal
        className={styles.popup}
        visible={!inviterRecordsLoading && !(account && isRegistered)}
      >
        <p>Please connect to wallet and sign up</p>
        <img src="/static/images/Investment/404.png" alt="not found" />
      </CustomModal> */}
    </MainContainer>
  );
};

export default Investment;
