import React, { useEffect, useState } from "react";
import { QuestionCircleOutlined } from "@ant-design/icons";
import { Input, message, Tooltip } from "antd";
import isEmail from "validator/lib/isEmail";
import { useTranslation } from "react-i18next";

import Button from "../../UI/Button/Button";
import { useIsMobileView } from "../../../hooks/useIsMobileView";
import {
  useIsRegistered,
  useRegisteredInvitationCodeContains,
  useRegisterInviter,
} from "../../../hooks/useInvitation";
import { useWallet } from "../../../hooks/useWallet";
import { useQuery } from "../../../hooks/useQuery";

import styles from "./Signup.module.scss";

const USERNAME_RULE = {
  min: 3,
  max: 32,
  regex: /^[a-zA-Z][a-zA-Z ]+[a-zA-Z]$/,
};

const Signup = ({ toggleShowModal }) => {
  const isMobileView = useIsMobileView();

  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [referralCode, setReferralCode] = useState("");
  const queryInviterCode = useQuery()?.get("referral");
  const { account } = useWallet();
  const { isValidInvitation } = useRegisteredInvitationCodeContains();
  const { registerInviter, state: registerInviterState } = useRegisterInviter();
  const { data: isRegistered, isLoading: isRegisteredLoading } =
    useIsRegistered();
  const { t } = useTranslation();
  const handleUsernameChange = (e) => {
    const value = e.target.value;
    setUsername(value);
  };
  const handleEmailChange = (e) => {
    const value = e.target.value;
    setEmail(value);
  };
  const handleReferralCodeChange = (e) => {
    const value = e.target.value;
    setReferralCode(value);
  };

  const handleRegisterInviter = async () => {
    if (!account || isRegisteredLoading || registerInviterState.isLoading) {
      return;
    }
    if (isRegistered) {
      message.error(t("Already registered"));
      return;
    }
    // validate username
    const trimmedUsername = username.trim();
    if (
      !(
        trimmedUsername &&
        trimmedUsername.length >= USERNAME_RULE.min &&
        trimmedUsername.length <= USERNAME_RULE.max &&
        trimmedUsername.match(USERNAME_RULE.regex)
      )
    ) {
      message.error(t("Username-alert"));
      return;
    }
    // validate email
    const trimmedEmail = email.trim();
    if (!isEmail(trimmedEmail)) {
      message.error(t("Invalid email"));
      return;
    }
    // validate referral code
    const trimmedReferralCode = referralCode.trim();
    if (!(await isValidInvitation(trimmedReferralCode))) {
      message.error(t("Invalid referral code"));
      return;
    }
    await registerInviter({
      username: trimmedUsername,
      email: trimmedEmail,
      inviterCode: trimmedReferralCode,
    });
    // TODO: toggle modal
    toggleShowModal();
  };

  useEffect(() => {
    if (queryInviterCode) {
      setReferralCode(queryInviterCode);
    }
  }, [queryInviterCode]);

  return (
    <div className={styles.Signup}>
      {!isMobileView && (
        <div className={styles.imgWrap}>
          <img src="/static/images/Profile/plant.png" alt="plant" />
        </div>
      )}
      <div className={styles.contentWrap}>
        <div className={styles.signupWrap}>
          <h2 className={styles.title}>{t("Sign Up")}</h2>
          <div className={styles.inputItem}>
            <h2>
              {t("Username")}
              <span style={{ color: "red" }}>*</span>{" "}
              <Tooltip title={t("Username-tooltip")}>
                <QuestionCircleOutlined />
              </Tooltip>
            </h2>
            <Input value={username} onChange={handleUsernameChange} />
          </div>
          <div className={styles.inputItem}>
            <h2>
              {t("Email")}
              <span style={{ color: "red" }}>*</span>
            </h2>
            <Input value={email} onChange={handleEmailChange} />
          </div>
          <div className={styles.inputItem}>
            <h2>
              {t("Referral Code")}
              <span style={{ color: "red" }}>*</span>
            </h2>
            <Input value={referralCode} onChange={handleReferralCodeChange} />
          </div>
          <Button
            className={styles.button}
            onClick={handleRegisterInviter}
            loading={registerInviterState.isLoading}
          >
            {t("Sign Up")}
          </Button>
        </div>
      </div>
    </div>
  );
};

export default Signup;
