import React from "react";
import { message } from "antd";
import CopyToClipboard from "react-copy-to-clipboard";
import classNames from "classnames";
import { useTakeBonus } from "../../../hooks/useInvestPool";
import { useWallet } from "../../../hooks/useWallet";
import { useTranslation } from "react-i18next";
import Button from "../../UI/Button/Button";

import styles from "./ProfileReferral.module.scss";
import { formatNumber, formatOutput } from "../../../utils/utils";
import { useTokenBalance } from "../../../hooks/useToken";
import { TOKENS } from "../../../constants/addresses";

const ProfileReferral = ({ investRecords, inviterRecords }) => {
  const { account } = useWallet();
  const { data: nativeBalance } = useTokenBalance(TOKENS.NATIVE);
  const { state: takeBonusState, takeBonus } = useTakeBonus();
  const referralLink = `${window.location.origin}/#/privatesale?referral=${inviterRecords?.invitationCode}`;
  const bonus = investRecords?.personRecord?.bonus;
  const sourceInvested = investRecords?.personRecord?.sourceInvested;
  // console.log(inviterRecords, investRecords);
  const { t } = useTranslation();
  const handleTakeBonus = () => {
    if (takeBonusState.isLoading) {
      return;
    }
    if (!bonus) {
      message.info(t("Nothing to claim"));
      return;
    }
    // if (nativeBalance < 0.005) {
    //   message.info(t("Need gas"));
    //   return;
    // }
    takeBonus();
  };
  return (
    <div className={styles.ProfileReferral}>
      <div className={styles.accountInfo}>
        <h2 className={styles.title}>{inviterRecords?.nickname}</h2>
        <div className={styles.infoItem}>
          <h2>{t("Wallet Address")}:</h2>
          <p>{formatOutput(account)}</p>
          <CopyToClipboard
            text={account}
            onCopy={() => {
              message.success(t("Address copied"));
            }}
          >
            <Button className={styles.addressCopyButton} secondary>
              {t("Copy")}
            </Button>
          </CopyToClipboard>
        </div>
        <div className={styles.infoItem}>
          <h2>{t("USDT-Balance")}:</h2>
          <p>{formatNumber(nativeBalance)} USDT</p>
        </div>
      </div>
      <hr />
      {!sourceInvested > 0 && (
        <h2 className={styles.title}>
          {t(
            "Please participate in private sale to activate referral system for additional referral bonus"
          )}
        </h2>
      )}
      {sourceInvested > 0 && (
        <div className={styles.referralInfo}>
          <div className={styles.statsWrap}>
            <h2 className={styles.title}>{t("Referral")}</h2>
            <div className={styles.statsContent}>
              <div className={styles.infoItem}>
                <h2>{t("Total Referral")}:</h2>
                <p>
                  {formatNumber(inviterRecords?.inviteeAmounts?.[0], "0,0")}
                </p>
              </div>
              <div className={styles.infoItem}>
                <h2>{t("Total Rewards")}:</h2>
                <p>{formatNumber(bonus)} BGF</p>
              </div>
              <Button
                className={styles.claimButton}
                onClick={handleTakeBonus}
                loading={takeBonusState.isLoading}
                disabled={!bonus}
              >
                {t("Claim")}
              </Button>
            </div>
          </div>
          <div className={styles.codeWrap}>
            <div className={classNames(styles.infoItem, styles.codeInfo)}>
              <h2>{t("Invitation Code")}</h2>
              <div className={styles.infoAndCopy}>
                <p>{formatOutput(inviterRecords?.invitationCode)}</p>
                <CopyToClipboard
                  text={inviterRecords?.invitationCode}
                  onCopy={() => {
                    message.success(t("Code copied"));
                  }}
                >
                  <Button className={styles.button}>{t("Copy")}</Button>
                </CopyToClipboard>
              </div>
            </div>
            <div className={classNames(styles.infoItem, styles.codeInfo)}>
              <h2>{t("Invitation Link")}</h2>
              <div className={styles.infoAndCopy}>
                <p>{referralLink}</p>
                <CopyToClipboard
                  text={referralLink}
                  onCopy={() => {
                    message.success(t("Link copied"));
                  }}
                >
                  <Button className={styles.button}>{t("Copy")}</Button>
                </CopyToClipboard>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default ProfileReferral;
