import classNames from "classnames";
import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import { NavLink } from "react-router-dom";
import {
  // useInviterRecords,
  useIsRegistered,
} from "../../../hooks/useInvitation";
import CustomModal from "../../UI/CustomModal/CustomModal";
import Signup from "../Signup/Signup";

import styles from "./ProfileButton.module.scss";

const ProfileButton = ({ onClick = () => {}, className }) => {
  const [showModal, setShowModal] = useState(false);
  const { data: isRegistered } = useIsRegistered();
  // const { data: inviterRecords } = useInviterRecords();
  const { t } = useTranslation();

  const toggleShowModal = () => {
    setShowModal((prev) => !prev);
  };

  // if (isRegisteredLoading || inviterRecordsLoading) {
  //   return;
  // }

  return (
    <>
      <div
        className={classNames(styles.ProfileButton, className)}
        onClick={onClick}
      >
        {isRegistered ? (
          <NavLink
            to="/profile"
            className={(isActive) =>
              classNames(styles.profileLink, {
                [styles.active]: isActive,
              })
            }
          >
            <img src="/static/images/Profile/user.svg" alt="user" />
            {t("My Profile")}
          </NavLink>
        ) : (
          <div className={styles.signupButton} onClick={toggleShowModal}>
            {t("Sign Up")}
          </div>
        )}
      </div>
      <CustomModal
        visible={showModal}
        onCancel={toggleShowModal}
        width={900}
        className={styles.signupModal}
      >
        <Signup toggleShowModal={toggleShowModal} />
      </CustomModal>
    </>
  );
};

export default ProfileButton;
